* {
  scroll-behavior: smooth;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  display: inline-block;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Roboto", sans-serif;
  margin: 0;
  font-weight: 400;
  font-size: 17px;
  line-height: 26px;
  color: #000;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 130px;
}

.default-btn {
  color: #fff;
  background: #a70b0b;
  box-shadow: 0 0 15px #dd9e23;
  font-size: 22px;
  line-height: 75px;
  text-align: center;
  width: 100%;
}

.text-red {
  color: #a70b0b;
}

.text-dark-yellow {
  color: #dd9e28;
  font-weight: bold;
}

.header {
  background: #663f04;
  background-image: url(../images/ellipse-fon.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__inner {
    position: relative;
    min-height: 665px;
    background-image: url(../images/header-fon.png);
    background-repeat: no-repeat;
    background-position: 550px bottom;
    color: #fff;
  }

  &__info {
    font-size: 9px;
    line-height: 11px;
    letter-spacing: 0.3em;
    padding: 5px 0 30px;
    opacity: 0.3;
  }

  &__title {
    font-family: "Vollkorn", serif;
    position: relative;
    padding: 55px 0 15px;
    margin-bottom: 20px;
    max-width: 440px;
    font-size: 40px;
    line-height: 50px;
    border-bottom: 10px solid #dbab00;
  }

  &__text {
    max-width: 440px;
    padding-bottom: 45px;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;
  }

  &__btn {
    max-width: 318px;
    font-size: 18px;
    line-height: 81px;
  }

  &__author {
    position: absolute;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    max-width: 535px;
    padding: 35px 235px 35px 40px;
    font-style: italic;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
  }

  &__image {
    position: absolute;
    right: 80px;
    bottom: -30px;
  }
}

.grats {
  &__inner {
    position: relative;
  }

  &__title-box {
    font-family: "Vollkorn", serif;
    font-size: 30px;
    line-height: 38px;
    text-align: center;
    font-weight: bold;
  }

  &__title-name {
    padding-top: 25px;
    width: 200px;
    margin: 0 auto;
  }

  &__title-grats {
    color: #a70b0b;
    line-height: 42px;
    padding-bottom: 60px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 65px;
      left: 160px;
      width: 600px;
      height: 10px;
      background-color: #dbab00;
    }
  }

  &__branch {
    position: absolute;
    display: flex;
    top: -30px;
    left: 30px;

    &1 {
      margin-right: 390px;
    }
  }

  &__content {
    display: flex;
    justify-content: space-between;
  }

  &__content-left {
    max-width: 575px;
  }

  &__content-title {
    font-weight: bold;
    font-size: 22px;
    line-height: 26px;
    padding-bottom: 20px;
  }

  &__content-text {
    font-size: 17px;
    line-height: 26px;
    padding-bottom: 25px;
  }

  &__content-delivery {
    background-color: rgba(242, 214, 116, 0.58);
    padding: 20px 55px;
    font-weight: 500;
    margin-bottom: 25px;
  }

  &__content-right {
    max-width: 275px;
    width: 100%;
    text-align: center;
  }

  &__dyploma {
    padding: 120px 0 70px;
    margin-bottom: 15px;
    background-image: url(../images/dyploma.jpg);
    background-size: cover;
    font-size: 10px;
    line-height: 10px;
    text-align: center;
    color: #d90000;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  }

  &__additional {
    margin-top: 50px;
  }

  &__delivery {
    font-family: "Vollkorn", serif;
    padding: 10px 65px;
    font-size: 30px;
    line-height: 42px;
    font-weight: 700;
    text-align: center;
    color: #a70b0b;
    border: 10px solid #dbab12;
    margin-bottom: 25px;
  }
}

.client {
  background: #663f04;
  background-image: url(../images/ellipse-fon.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__inner {
    position: relative;
    min-height: 650px;
    background-image: url(../images/client-fon.png);
    background-repeat: no-repeat;
    background-position: 600px bottom;
    padding-top: 45px;
    padding-bottom: 150px;
    color: #fff;
  }
  
  &__title-box {
    font-family: "Vollkorn", serif;
    text-align: center;
    font-size: 30px;
    line-height: 38px;
  }

  &__title-name {
    line-height: 42px;
    padding-bottom: 10px;
  }

  &__title-grats {
    font-weight: 700;
    color: #ffe484;
    text-shadow: 0 0 50px #f1ce30;
    max-width: 435px;
    margin: 0 auto;
    padding: 15px 0 35px;
    border-bottom: 10px solid #dbab00;
    margin-bottom: 50px;
  }

  &__text {
    max-width: 410px;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;
  }
}

.delivery {
  &__inner {
    position: relative;
    padding-top: 45px;
    padding-bottom: 40px;
  }

  &__title {
    position: relative;
    font-family: "Vollkorn", serif;
    font-size: 40px;
    line-height: 56px;
    color: #030303;
    text-align: center;
    font-weight: bold;
    padding-bottom: 105px;

    
    &::before {
      content: "";
      position: absolute;
      top: 158px;
      left: 165px;
      width: 600px;
      height: 10px;
      background-color: #dbab00;
    }
  }

  &__branch {
    position: absolute;
    display: flex;
    top: -30px;
    left: 30px;

    &1 {
      margin-right: 390px;
    }
  }

  &__content {
    display: flex;
    justify-content: space-between;
  }

  &__content-title {
    font-weight: bold;
    font-size: 30px;
    line-height: 32px;
    padding: 15px 0 30px;
  }

  &__content-text {
    font-size: 20px;
    line-height: 26px;
    max-width: 470px;
  }

  &__content-right {
    min-height: 300px;
    max-width: 345px;
    width: 100%;
    background-image: url(../images/blank.png);
    background-repeat: no-repeat;
    text-align: center;
    padding: 35px 0 0 25px;
  }

  &__delivery {
    font-family: "Vollkorn", serif;
    padding: 20px 130px;
    font-size: 30px;
    line-height: 42px;
    font-weight: 700;
    text-align: center;
    color: #a70b0b;
    border: 10px solid #dbab12;
  }
}

.instruction {
  background-color: #663f04;
  color: #fff;
  background-image: url(../images/ellipse-fon.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  
  &__inner {
    padding-top: 50px;
    padding-bottom: 55px;
  }

  &__title {
    max-width: 890px;
    margin: 0 auto 125px;
    font-family: "Vollkorn", serif;
    font-size: 40px;
    line-height: 50px;
    text-align: center;
    padding-bottom: 20px;
    border-bottom: 10px solid #dbab00;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    padding-bottom: 45px;
  }

  &__item {
    position: relative;
    display: flex;
    max-width: 205px;

    &::after,
    &::before {
      content: "";
      position: absolute;
      background-repeat: no-repeat;
      width: 100px;
      height: 100px;
    }

    &::after {
      top: -65px;
      left: 65px;
      background-image: url(../images/list.png);
    }

    &:not(:first-child)::before {
      background-image: url(../images/arrow.png);
      top: -55px;
      left: -80px;
    }

    &:nth-child(1)::before {
      background-image: url(../images/arrow-down.png);
      top: -65px;
      left: 70px;
    }

    &:nth-child(1)::after {
      width: 39px;
      height: 3px;
      background-color: #fff;
      top: -23px;
      left: 65px;
    }

    &:nth-child(3)::after {
      background-image: url(../images/hand.png);
    }

    &:nth-child(4)::after {
      background-image: url(../images/fura.png);
    }
  }

  &__number {
    color: #dbab00;
    font-weight: bold;
    font-size: 40px;
    line-height: 38px;
    padding-right: 5px;
  }

  &__item-text {
    font-weight: 900;
    font-size: 14px;
    line-height: 16px;
  }

  &__text {
    font-family: "Vollkorn", serif;
    font-size: 25px;
    line-height: 31px;
    text-align: center;
    padding: 0 75px 50px;
  }

  &__warning {
    padding: 10px 85px 20px;
    background-color: #f5e586;
    border: 10px solid #dbab00;
    font-family: "Vollkorn", serif;
    color: #f00;
    font-weight: 700;
    font-size: 40px;
    line-height: 45px;
    text-align: center;
  }
}

.blank {
  padding: 70px 0 20px;

  &__form {
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.3);
    padding: 20px 25px;
    margin-bottom: 35px;
  }

  &__form-content {
    border: 5px solid #dbab00;
    padding: 25px 85px 45px;
  }

  &__form-title {
    font-weight: bold;
    font-size: 44px;
    line-height: 46px;
    text-align: center;
    text-transform: uppercase;
    padding-bottom: 25px;
  }

  &__form-data {
    font-size: 18px;
    line-height: 23px;
    padding: 0 0 40px 40px;
  }

  &__form-text {
    font-size: 18px;
    line-height: 23px;
  }

  &__form-radio {
    padding-bottom: 10px;
  }

  &__form-radio-label {
    display: inline-block;
    position: relative;
    padding-left: 25px;
    margin: 20px 25px 0 0;
    cursor: pointer;
    font-size: 10px;
    line-height: 13px;
    user-select: none;

    &2 {
      margin-left: 10px;
    }

    input {
      position: absolute;
      opacity: 0;
      height: 0;
      width: 0;
    }
  }

  &__form-radio-checkmark {
    position: absolute;
    top: -4px;
    left: 0;
    height: 19px;
    width: 19px;
    border-radius: 50%;
    border: 2px solid #e2c765;
  }

  input:checked ~ .blank__form-radio-checkmark {
    background-image: url(../images/radio-checkmark.png);
    background-repeat: no-repeat;
    background-position: center;
  }

  &__form-input {
    font-size: 18px;
    line-height: 23px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-bottom: 15px;

    input {
      display: block;
      width: 100%;
      padding-left: 30px;
      font-size: 14px;
      line-height: 45px;
      border: 2px solid #e2c765;
    }
  }
   
  &__form-day {
    max-width: 275px;

    input {
      margin-top: 15px;
    }
  }

  &__form-phone {
    max-width: 310px;
  }

  &__form-delivery {
    font-size: 18px;
    line-height: 23px;

    input {
      display: block;
      width: 100%;
      font-size: 14px;
      line-height: 45px;
      border: 2px solid #e2c765;
      margin-top: 7px;
      padding-left: 30px;
      margin-bottom: 25px;
    }
  }

  &__form-checkbox {
    padding-left: 55px;
  }

  &__form-refusal {
    padding-bottom: 15px;
    display: block;
    color: rgba(0, 0, 0, 0.5);

    .blank__form-checkbox-checkmark {
      top: 10px !important;
    }
  }

  &__form-refusal,
  &__form-agree {
    font-size: 18px;
    line-height: 23px;
    display: block;
    position: relative;
    cursor: pointer;
    user-select: none;

    input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;
    }

    .blank__form-checkbox-checkmark {
      position: absolute;
      top: 0;
      left: -55px;
      height: 20px;
      width: 20px;
      border: 2px solid #e2c765;
    }

    & input:checked ~ .blank__form-checkbox-checkmark {
      background-image: url(../images/checkbox-checkmark.png);
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  &__content {
    text-align: center;
  }

  &__btn {
    max-width: 275px;
    margin-bottom: 40px;
  }

  &__text {
    font-family: "Vollkorn", serif;
    font-weight: 700;
    font-size: 40px;
    line-height: 56px;
    padding-bottom: 40px;
  }

  &__info {
    text-align: left;
    font-size: 9px;
    line-height: 11px;
    letter-spacing: 0.3em;
    opacity: 0.3;
  }

  &__info-text {
    padding-top: 20px;
  }
}
